:root{
    --lightblue: rgb(237, 237, 253);
    --darkblue:  rgb(202, 202, 219);
}

body{
    font-family: "Raleway",sans-serif;
    margin: 0;
    padding: 0;
}

.contentFlex{
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    max-width: 100%;
    max-height: 100%;
}

.navBar{
    position: absolute;
    top: 10px;
    left: 10px;
    display: flex;
    flex-direction: column;
    background-color: var(--lightblue);
    border-radius: 10px;
    padding: 5px;
}

.listContainer{
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.entry{
    padding: 7px 16px;
    display: block;
    user-select: none;
    border-radius: 10px;
}

.entryGroup li{
    font-weight: 600;
}

.entryGroup:hover{
    cursor:default;
}

.entryRelease{
    position: relative;
}

.entryRelease li{
    font-weight: 400;
    text-indent: 10px;
}

.entryRelease:hover{
    background-color: var(--darkblue);
}

.entryRelease:hover{
    cursor: pointer;
}

.entryRelease:hover .dropDown{
    display: flex;
    flex-direction: column;
}

.dropDown{
    display: none;
    position: absolute;
    top: 0;
    left: 100%;
    z-index: 1;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    background-color: var(--lightblue);
    border-radius: 10px;
    padding:5px;
}

.dropDown a{
    padding: 7px 16px;
    white-space: nowrap;
}

.dropDown a:hover{
    background-color: var(--darkblue);
    border-radius: 10px;
}

.pdfReader{
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    max-height: 100%;
    margin: 0;
    padding: 0;
    border-style: none;
    position:absolute;
    top:0;
    left:0;
    z-index: -1;
}

.closeButton{
    font-weight: 600;
    background-color:var(--lightblue);
    border-style: none;
    border-radius: 10px;
    padding:5px 20px;
}

.closeButton:hover{
    background-color:var(--darkblue);
}